import React from 'react';
import PropTypes from 'prop-types';
import clsx from 'clsx';

import './Container.scss';

/**
 *       .
 *
 * @param {string} className -   CSS.
 * @param {ReactNode} children -  .
 * @param {boolean} border -   ().
 * @param {string} direction -   ('row'  'column').
 * @returns {JSX.Element}
 */
const Container = ({ className, children, border, direction }) => {
  const currentClassName = clsx(
    'container',
    className,
    border && 'container_border',
    direction && `container_${direction}`
  );

  return <div className={currentClassName}>{children}</div>;
};

Container.propTypes = {
  className: PropTypes.string,
  direction: PropTypes.oneOf(['row', 'column']).isRequired,
  children: PropTypes.node,
  border: PropTypes.bool,
};

Container.defaultProps = {
  className: '',
  direction: 'column',
  border: false,
};

export { Container };
